<template>
    <div class="txt-box">
            <h3 :class="getClass">
                {{text}}
            </h3>
            <button @click="onToggleMore">{{getBtnTxt}}</button>
        </div>
</template>

<script>
    export default {
        props: ['isLongTxtShown', 'text', 'isButton'],
        computed: {
            getClass() {
                return (this.isLongTxtShown) ? 'long' : 'short'
            },
            getBtnTxt() {
                return (this.isLongTxtShown) ? 'More' : 'Less'
            }
        },
        methods: {
            onToggleMore() {
                this.$emit('toggle', 'hey')
            }
        },   
    }
</script>

export function LongTxt(props) {

    var descClass = (props.isLongTxtShown) ? 'long' : 'short'
    var buttonTxt = (props.isLongTxtShown) ? 'More' : 'Less'

    return (
        <div class="txt-box">
            <h3 class={descClass}>
                {props.text}
            </h3>
            {props.isButton && <button onClick={props.toggleMore}>{buttonTxt}</button>}
        </div>
    )
}
