<template>
  <section class="add-book">
    <input
      v-model="searchKey"
      @input="onChange"
      class="styled-input"
      type="text"
    />
    <ul v-if="searchKey">
      <li class="add-book-title" v-for="book in newBooks" :key="book.id">
        <p>{{ book.volumeInfo.title }}</p>
        <button @click="onAddBook($event, book)" class="add-book-title-btn">
          +
        </button>
      </li>
    </ul>
  </section>
</template>

<script>
import { bookService } from "../services/bookService";
export default {
  data() {
    return {
      searchKey: "",
      newBooks: [],
    };
  },

  //   async created() {
  //   },

  methods: {
    onChange(ev) {
      this.getBooks(ev.target.value);
    },

    async getBooks(searchTxt) {
      if (!searchTxt) {
        this.newBooks = [];
        return;
      }
      
      const books = await bookService.getGoogleBooks(searchTxt);
      this.newBooks = books;
    },

    onAddBook(ev, book) {
      console.log("onAddBook -> book", book);

      this.$store.dispatch({ type: "saveBook", book });
    },
  },
};
</script>