<template>
  <section class="user-msg" v-show="msg">
    <p>{{ msg }}</p>
    <button @click="closeMsg">X</button>
  </section>
</template>

<script>
import eventBus from "@/services/eventBus.service.js";

export default {
  data() {
    return {
      msg: "",
    };
  },
  created() {
    eventBus.$on("showMsg", (msg) => {
      this.msg = msg;
      setTimeout(() => {
        this.msg = "";
      }, 2000);
    });
  },
  destroyed() {
    eventBus.$off("showMsg");
  },

  methods: {
    closeMsg() {
      this.msg=''
    }
  }
};
</script>
