<template>
  <section class="main-filter main-container flex">
    <input
      class="styled-input"
      type="text"
      placeholder="Search"
      v-model="filterBy.title"
      @input="emitFilter"
    />
    <input
      class="styled-input"
      type="number"
      placeholder="Min Price"
      v-model="filterBy.minPrice"
      @input="emitFilter"
    />
    <input
      class="styled-input"
      type="number"
      placeholder="Max Price"
      v-model="filterBy.maxPrice"
      @input="emitFilter"
    />
  </section>
</template>

<script>
export default {
  data() {
    return {
      filterBy: {
        title: "",
        maxPrice: Infinity,
        minPrice: -Infinity,
      },
    };
  },
  methods: {
    emitFilter() {
      this.$emit("filtered", this.filterBy);
    },
  },
};
</script>