
import { bookService } from '../../services/bookService'


export default ({
    state: {
        books: []
    },

    getters: {
        booksToShow(state) {
            const books = state.books
            if (!books.length) return []
            return books
        }
    },

    mutations: {
        setBooks(state, { books }) {
            state.books = books
        },

        saveBook(state, { book }) {
            state.books.push(book)
        },

        removeBook(state, { bookId }) {
            state.books = state.books.filter(book => book.id !== bookId)
        },
    },
    actions: {
        async loadBooks({ commit }) {
            const books = await bookService.query()
            commit({ type: 'setBooks', books })
        },

        async saveBook({ commit }, { book }) {
            const newBook = bookService.addGoogleBook(book)
            commit({ type: 'saveBook', book: newBook })
        },
        async removeBook({ commit }, { bookId }) {
            await bookService.remove(bookId);
            commit({ type: 'removeBook', bookId })
        },


    },
    modules: {
    }
})