import Vue from 'vue'
import VueRouter from 'vue-router'
import BookApp from '../views/BookApp.vue'
import BookDetails from '../cmps/BookDetails'

Vue.use(VueRouter)
// import BookDetails from '@/cmps/BookDetails'
const routes = [
  {
    path: '/details/:id',
    name: 'BookDetails',
    component: BookDetails,

    children: [
      {
        path: '/details/:id/editReview/:id?',
        name: 'BookDetails',
        component: BookDetails,
      },
    ]
  },
  {
    path: '/',
    name: 'BookApp',
    component: BookApp,

  },

  // {
  //   path: '/book',
  //   name: 'BookApp',
  //   component: BookApp
  // },

]

const router = new VueRouter({
  routes
})

export default router
