<template>
  <section class="main-reviews flex">
    <section class="reviews-list">
      <h2>Reviews</h2>
      <div v-for="(review, idx) in reviews" :key="idx" class="review-prev">
        <p>name: {{ review.name }}</p>
        <p>text: {{ review.text }}</p>
        <p>rating: {{ getStars(review) }}</p>
        <button @click="onRemove(review.id)">X</button>
      </div>
    </section>
    <add-review :bookId="bookId" @addReview="saveReview" />
  </section>
</template>

<script>
import { bookService } from "../services/bookService";
import { utils } from "../services/utils";
import AddReview from "./AddReview.vue";
export default {
  components: { AddReview },
  data() {
    return {};
  },

  methods: {
    getStars(review) {
      return `${review.rating} stars`;
    },
    async saveReview(review, bookId) {
      review.id = utils.makeId();
      await bookService.saveReview(review, bookId);
    },

    async onRemove(reviewId, bookId) {
      bookService.removeReview(reviewId, this.bookId);
    },
  },
  props: ["reviews", "bookId"],
};
</script>