<template>
  <section @click="onSelectBook" class="book-preview">
    <h1>{{ book.title }}</h1>
    <h4>{{ book.authors[0] }}</h4>
    <img class="prev-img" :src="book.thumbnail" alt="" />
    <p
      :class="[
        'bold',
        {
          red: book.listPrice.amount > 150,
          green: book.listPrice.amount < 20,
        },
      ]"
    >
      price: {{getCurrSymbol(book.listPrice.currencyCode)}}{{book.listPrice.amount }}
    </p>
    <button @click.stop="onRemove" class="remove-book styled-button">X</button>
  </section>
</template>

<script>
import { bookService } from '../services/bookService';
export default {
  data() {
    return {};
  },

  props: ["book"],

  methods: {
    onRemove() {
      this.$emit("remove", this.book.id);
    },

    onSelectBook() {
      this.$emit("selected", this.book);
    },
    getCurrSymbol(currencyCode) {
      return bookService.getCurrencySymbol(currencyCode);
    },
  },
};
</script>