
<template>
  <section class="add-review">
    <form class="add-form" @submit.prevent="onAddReview">
      <input class="add styled-input" type="text" v-model="review.name" />
      <select class="add" v-model="review.rating">
        <option value="1">1</option>
        <option value="2">2</option>
        <option value="3">3</option>
        <option value="4">4</option>
        <option value="5">5</option>
      </select>
      <textarea
        v-model="review.text"
        class="add"
        name=""
        id=""
        cols="30"
        rows="10"
      />

      <button class="save-review">Save</button>
    </form>
  </section>
</template>

<script>
import eventBus from '../services/eventBus.service.js'
export default {
  
  data() {
    return {
      review: {
        name: "",
        text: "",
        rating: 1,
      },
    };
  },
  methods: {
    
    onAddReview() {
      this.$emit("addReview", this.review, this.bookId);
      eventBus.$emit('showMsg', 'Review Added');
      
      this.review = { name: "", text: "", rating: 1 };
    },
  },

  props: ["bookId"],
};
</script>