<template>
  <div v-if="book" class="book-details-container main-container flex column">
    <button @click="onBack" class="styled-button-reg btn">Back</button>
    <div class="book-main-details flex">
      <section class="details">
        <div class="book-desc flex">
          <p>{{ this.getBookLengthDesc() }}</p>
          <p>{{ this.getBookAgeDesc() }}</p>
        </div>

        <div class="details-title">
          <p>Title</p>
          <h3>{{ book.title }}</h3>
        </div>
        <div class="subtitle">
          <p>Subtitle</p>
          <h3>{{ book.subtitle }}</h3>
        </div>
        <div class="authors">
          <p>Authors:</p>
          <h3>{{ book.authors.toString() }}</h3>
        </div>
        <div class="published-date">
          <p>published Date:</p>
          <h3>{{ book.publishedDate }}</h3>
        </div>
        <LongText
          @toggle="toggleMore"
          :text="book.description"
          :isLongTxtShown="isLongTxtShown"
          :isButton="isButton"
        />
      </section>
      <div class="img-box">
        <img class="img-details" :src="book.thumbnail" alt="" />
      </div>
    </div>

    <div class="description flex column">
      <section class="one">
        <div class="page-count flex">
          <p>Page Count:</p>
          <h3>{{ book.pageCount }}</h3>
        </div>
        <div class="categories flex">
          <p>Categories:</p>
          <h3>
            <span class="span-ctg">{{ book.categories.toString() }}</span>
          </h3>
        </div>
      </section>
      <section class="two">
        <div class="language flex">
          <p>Language:</p>
          <h3>{{ book.language }}</h3>
        </div>
        <div class="details-price flex">
          <p>Price:</p>
          <h3>
            {{ this.getCurrSymbol(book.listPrice.currencyCode)
            }}{{ book.listPrice.amount }}
          </h3>
        </div>
      </section>
    </div>

    <reviews :bookId="book.id" :reviews="book.reviews" />
  </div>
</template>
        

<script>
import Reviews from "./Reviews.vue";
import { bookService } from "../services/bookService";
import LongText from "./LongText.vue";
export default {
  data() {
    return {
      book: null,
      isLongTxtShown: true,
      isButton: true,
    };
  },

  async created() {
    const { id } = this.$route.params;
    const book = await bookService.getById(id);
    this.book = book;
  },

  methods: {
    toggleMore() {
      this.isLongTxtShown = !this.isLongTxtShown;
    },
    getCurrSymbol(currencyCode) {
      return bookService.getCurrencySymbol(currencyCode);
    },
    getBookLengthDesc() {
      const bookLength = this.book.pageCount;
      if (bookLength > 500) {
        return "Long Reading";
      } else if (bookLength > 200 && bookLength <= 500) {
        return "Decent Reading";
      } else {
        return "Light Reading";
      }
    },

    getBookAgeDesc() {
      const bookAge = 2020 - +this.book.publishedDate;
      if (bookAge > 10) {
        return "Veteran Book";
      } else if (bookAge <= 1) {
        return "New!";
      }
    },

    onBack() {
      this.$router.push("/");
    },
  },

  components: {
    LongText,
    Reviews,
  },
};
</script>

