

<template>
  <section class="main-book main-container">
    <add-book v-if="isAddBook" />
    <book-filter v-if="!isAddBook" @filtered="setFilter"></book-filter>
    <div v-if="!isAddBook" class="add-book-btn">
      <button @click="toggleIsAdd">Add New Book</button>
    </div>
    <book-list
      v-if="!isAddBook && booksToShow.length"
      @remove="onRemove"
      :books="booksToShow"
      @selected="selectBook"
    ></book-list>
  </section>
</template>

<script>
import AddBook from "../cmps/AddBook.vue";
import BookDetails from "../cmps/BookDetails.vue";
import BookFilter from "../cmps/BookFilter.vue";
import BookList from "../cmps/BookList.vue";
import { bookService } from "../services/bookService.js";
// import  from ''
export default {
  data() {
    return {
      selectedBook: null,
      isAddBook: false,
      filterBy: {
        title: "",
        maxPrice: Infinity,
        minPrice: -Infinity,
      },
    };
  },

  async created() {
    await this.loadBooks();
  },

  computed: {
    booksToShow() {
      const books = this.$store.getters.booksToShow;
      // if (!books.length) return []

      let filterByTitle = books.filter((book) =>
        book.title.toLowerCase().includes(this.filterBy.title.toLowerCase())
      );
      let filterByMax = filterByTitle.filter(
        (book) => book.listPrice.amount <= this.filterBy.maxPrice
      );
      let filterByAll = filterByMax.filter(
        (book) => book.listPrice.amount >= this.filterBy.minPrice
      );
      return filterByAll;
    },
  },

  methods: {
    setFilter(filterBy) {
      if (!filterBy.maxPrice) filterBy.maxPrice = Infinity;
      if (!filterBy.minPrice || filterBy.minPrice >= filterBy.maxPrice) {
        filterBy.minPrice = -Infinity;
      }
      this.filterBy = filterBy;
    },

    toggleIsAdd() {
      this.isAddBook = !this.isAddBook;
    },

    async loadBooks() {
      try {
        await this.$store.dispatch({ type: "loadBooks" });
      } catch {
        console.log("error");
      }
    },
    async onRemove(bookId) {
      this.$store.dispatch({ type: "removeBook", bookId });
    },

    async selectBook(book) {
      this.selectedBook = book;
      this.$router.push(`/details/${book.id}`);
    },

    resetSelectedBook() {
      this.selectedBook = null;
    },
  },

  components: {
    BookDetails,
    BookFilter,
    BookList,
    AddBook,
  },
};
</script>

