<template>
  <div>
    <section v-if="books.length" class="book-list">
      <book-preview
        @selected="onSelectBook"
        @remove="onRemove"
        v-for="book in books"
        :book="book"
        :key="book.id"
      />
    </section>
    <section v-else>
      <p>No results</p>
    </section>
  </div>
</template>

<script>
import BookPreview from "./BookPreview.vue";
export default {
  data() {
    return {};
  },
  props: ["books"],

  components: {
    BookPreview,
  },

  created() {
  },

  methods: {
    onRemove(id) {
      this.$emit("remove", id);
    },

    onSelectBook(book) {
      this.$emit("selected", book);
    },
  },
};
</script>


